* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style-type: none;
  box-sizing: border-box;
}
body {
  width: 100%;
  max-width: 600px;
  border: 1px solid #c1c1c1;
  margin: 30px auto;
  border-radius: 5px;
}
input,
.addTodo {
  padding: 5px;
}
form {
  padding: 30px;
}
form,
.addTodo {
  display: grid;
  grid-gap: 10px;
  border-radius: 7px;
}
.addTodo {
  display: grid;
  margin: 10px 20px;
  border: 1px solid #c1c1c1;
}
.description {
  grid-column: span 2;
  padding: 12px 0 50px 20px;
}
.task {
  grid-column: span 2;
  padding: 8px 5px;
}
.date {
  grid-column: span 2;
  padding: 8px 5px;
}
input:focus {
  border-color: silver;

  outline: none;
}
.addTodo-item {
  margin: 0 10px;
}
button {
  padding: 5px 10px;
  margin-top: 0 auto;
  border-radius: 5px;
  border: none;
  background-color: #3ded97;
  color: grey;
  align-self: center;
  justify-self: center;
}
.complete-button {
  margin: 0 10px;
}
